<template>
  <div>
    <div class="header">
      <p @click="returnBack">关闭</p>
      <input
        type="search"
        v-model.trim.lazy="search"
        @keyup.enter="goSearch"
      >
    </div>
    <ul class="has-header">
      <li>
        <div>
          <a
            href="#"
            target="_blank"
          >
            <strong style="color: rgb(35, 132, 232);">电影</strong>
            <span>影院热映</span>
          </a>
          <a
            href="#"
            target="_blank"
          >
            <strong style="color: rgb(230, 70, 126);">同城</strong>
            <span>周末活动</span>
          </a>
          <a
            href="#"
            target="_blank"
          >
            <strong style="color: rgb(159, 120, 96);">阅读</strong>
            <span>电子书</span>
          </a>
          <a
            href="#"
            target="_blank"
          >
            <strong style="color: rgb(225, 100, 77);">东西</strong>
            <span>心爱之物</span>
          </a>
        </div>
      </li>
      <li>
        <div>
          <a
            href="#"
            target="_blank"
          >
            <strong style="color: rgb(122, 106, 219);">电视</strong>
            <span>正在热播</span>
          </a>
          <a
            href="#"
            target="_blank"
          >
            <strong style="color: rgb(42, 184, 204);">小组</strong>
            <span>志趣相投</span>
          </a>
          <a
            href="#"
            target="_blank"
          >
            <strong style="color: rgb(87, 116, 197);">游戏</strong>
            <span>虚拟世界</span>
          </a>
          <a
            href="#"
            target="_blank"
          >
            <strong style="color: rgb(64, 207, 169);">FM</strong>
            <span>红心歌单</span>
          </a>
        </div>
      </li>
      <li>
        <div>
          <a
            href="#"
            target="_blank"
          >
            <strong style="color: rgb(159, 120, 96);">图书</strong>
            <span>畅销排行</span>
          </a>
          <a
            href="#"
            target="_blank"
          >
            <strong style="color: rgb(244, 143, 46);">音乐</strong>
            <span>新碟榜</span>
          </a>
          <a
            href="#"
            target="_blank"
          >
            <strong style="color: rgb(89, 108, 221);">应用</strong>
            <span>玩手机</span>
          </a>
          <a
            href="#"
            target="_blank"
          >
            <strong style="color: rgb(66, 189, 86);">市集</strong>
            <span>购买原创</span>
          </a>
        </div>
      </li>
    </ul>
    <div class="login">
      <ul class="logincard">
        <li @click="goToLogin">注册账号</li>
        <li>登陆账号</li>
        <li>使用桌面版</li>
        <li>使用豆瓣App</li>
      </ul>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      a: 1,
      search: ''
    }
  },
  methods: {
    returnBack () {
      this.$router.back()
    },
    goToLogin () {
      this.$router.push('/login')
    },
    goSearch () {
      this.$router.push({ path: '/search', query: { q: this.search } })
      this.search = ''
    }
  }
}
</script>

<style lang="scss" scoped>
.header {
  box-sizing: border-box;
  padding: 10px;
  position: relative;
  z-index: 9999;
  width: 100%;
  top: 0;
  bottom: 0px;
  font-size: 20px;
  display: flex;
  align-items: center;
  height: 70px;
  line-height: 70px;
  background: #fff;
  color: greenyellow;
  border-bottom: 1px solid #f3f3f3;
  input {
    height: 30px;
    flex: 1;
    border: 0px;
    background: url(data:image/png;base64,)
      no-repeat 50% #f3f3f3;
    margin-right: 25px;
    border-radius: 0.3rem;
    background-size: 1.5rem;
  }
  input:focus {
    background: #f3f3f3;
  }
  p {
    padding: 0 10px;
  }
}

.has-header {
  display: flex;
  justify-content: space-around;
  li {
    width: 33.3%;
    div {
      a {
        strong {
          margin-top: 1.1rem;
          text-align: center;
          font-size: 1.5rem;
          display: block;
          font-weight: 600;
        }
        span {
          text-align: center;
          font-size: 0.8rem;
          display: block;
        }
      }
    }
  }
}

.logincard {
  margin-top: 20px;
  display: flex;
  flex-wrap: wrap;
  li {
    padding: 10px 0px;
    box-sizing: border-box;
    border: 1px solid #f3f3f3;
    color: #42bd56;
    text-align: center;
    font-size: 1.1rem;
    width: 50%;
  }
}
</style>



